.popup__close {
  background-image: url('../../../images/Close_Icon.svg');
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
  background-color: transparent;
  width: 32px;
  height: 32px;
  cursor: pointer;
  position: absolute;
  top: -40px;
  right: -40px;
}

.popup__close:hover {
  opacity: 0.6;
}

@media screen and (max-width: 767px) {
  .popup__close {
    width: 20px;
    height: 20px;
    top: -36px;
    right: 0;
  }
}
